<template>
    <!-- teams-area -->
    <div class="teams-area row">
        <!-- teams -->
        <div class="col-md-3" v-for="team in teams"  :key="team.tm_id">
            <div class="team-card">
                <div class="title">
                    <h3><i class="fa fa-users"></i> {{team.tm_name}}</h3>
                </div>
            </div>
        </div>
        <!-- teams -->
        
        <!-- add teams -->
        <div class="col-md-3 ">
            <div class="team-card p-0">
                <a class="add-team" href="#" data-toggle="modal" data-target="#addTeamModal">
                    <img class="img-fluid" src="/img/23. Close.bdb0a88d.svg" alt=""> Add new
                </a>
            </div>
        </div>
        <!-- add teams -->

    </div>
    <!-- saleteam-area -->
</template>

<script>
import axios from 'axios'

export default {
    name: 'TeamsGrid',
    data(){
		return{
            teams: null,
		}
	},
	created(){
		this.getTeams()
	},
	methods:{
		getTeams(){
			axios
			.get('teams')
			.then( res => {
				const {data:{data}} = res
                this.teams = data
                this.$root.$on('newTeam', function(newTeam_data) {
                    data.push(newTeam_data)
                })
				console.log(res)
			})
			.catch(err =>{
				console.log(err)
			})
		}
    }
}
</script>

<style scoped>
.team-card {
    background: #fff;
    border-radius: 10px;
    margin-bottom: 25px;
    padding: 15px;
    -webkit-box-shadow: -18px 8px 14px rgba(49, 81, 161, 0.12);
    box-shadow: 0px 8px 14px rgba(49, 81, 161, 0.12);
}
.team-card .title h3{
    color: #343945;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.5;
    text-transform: capitalize;
}
.team-card .title h3 i{
    color: #3151a1;
}
.team-card .add-team{
    display: flex;
    background: #3151a1;
    border-radius: 10px;
    padding: 15px;
    font-size: 17px;
    font-weight: 700;
    color: #fff;
    text-decoration: none;
}
.team-card .add-team img{
    margin-right: 5px;
    width:25px;
}
</style>